

import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Divider from '@mui/material/Divider';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';

import Fade from '@mui/material/Fade';
import Paper from '@mui/material/Paper';
import Popper, { PopperPlacementType } from '@mui/material/Popper';
import image from './test.jpg'



function User({data,divider,kk}){
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [open, setOpen] = React.useState(false);
    const [placement, setPlacement] = React.useState();
    
    const handleClick =
      (isOpen) =>
      (event) => {
        setAnchorEl(event.currentTarget);
        setOpen((prev) => isOpen);
        setPlacement("top");
      };

    return (
        <Typography sx={{backgroundColor:data.color?data.color:"transparent"}}>
        <Popper open={open} anchorEl={anchorEl} placement={placement} transition>
        {({ TransitionProps }) => (
          <Fade {...TransitionProps} timeout={100}>
            <Paper>
              <Typography sx={{display:'flex',flexDirection:'row',padding:1,alignItems:'center'}}>
              <Avatar alt={data.name} src={image} sx={{ width: 150, height: 150 }}/>
              <Typography sx={{display:'flex',flexDirection:'column',margin:1}}>
                <Typography sx={{fontWeight:'bold'}}>Abderafie Chairi</Typography>
                <Typography>IT Consultant</Typography>
              </Typography>
              </Typography>
            </Paper>
          </Fade>
        )}
      </Popper>
      <Typography onMouseLeave={handleClick(false)} onMouseEnter={handleClick(true)}>
        <ListItem alignItems="flex-start" >
        <ListItemAvatar>
                <Avatar alt={data.name} src={image} />
        </ListItemAvatar>
        <ListItemText
          primary={data.job}
          secondary={
            <React.Fragment>
              <Typography
                sx={{ display: 'inline' }}
                component="span"
                variant="body2"
                color="text.primary"
              >
                {data.name}
              </Typography>
              {" "+data.msg+" : "+kk}
            </React.Fragment> 
          }
        />
      </ListItem>
      </Typography>
      {divider&&<Divider variant="inset" component="li" />}
      </Typography>
    )
}


export default function ListUsers({data}) {

  return (
    <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper',margin:1}}>
        {data.map((i,k)=><User key={k} data={i} kk={k} divider={k!=data.length}/>)}
    </List>
  );
}
