import * as React from 'react';
import Timeline from '@mui/lab/Timeline';
import './Event.css';
// import Timeline from '@muiz';

import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineDot from '@mui/lab/TimelineDot';
import TimelineOppositeContent from '@mui/lab/TimelineOppositeContent';

function Item({data}){
    return(
        <TimelineItem>
          <TimelineOppositeContent color="text.secondary">
            {data.date}
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot color='grey'/>
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent>{data.data}</TimelineContent>
        </TimelineItem>        
    )
}


export default function Events() {
    const [data,setData] = React.useState([
        {date:'10/11/2022',data:'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam'},
        {date:'21/11/2022',data:'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam'},
        {date:'15/12/2022',data:'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam'},
        {date:'31/12/2022',data:'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam'},
    ])
  return (
    <React.Fragment>
        <h1 className='title-event'>Events</h1>  
      <Timeline position="alternate">
        {data.map((i,k)=><Item key={k} data={i}/>)}
      </Timeline>
    </React.Fragment>
  );
}
