import * as React from 'react';
import Typography from '@mui/material/Typography';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import './Blog.css';
function BlogList(props) {
    const [data,_]=React.useState([
        {title:'Ardouino',subtitle:'Open-source electronic prototyping....'},
        {title:'Ardouino',subtitle:'Open-source electronic prototyping....'},
        {title:'Ardouino',subtitle:'Open-source electronic prototyping....'},
        {title:'Ardouino',subtitle:'Open-source electronic prototyping....'}
    ])
    return ( 
        <Typography sx={{margin:'auto',maxWidth:500,marginBottom:20}}>
        <Typography sx={{textAlign:'center',fontWeight:'bold',fontSize:24}}>Articles</Typography>
        
        <List sx={{ width: '100%', bgcolor: 'background.paper' }}>
            {data.map((i,k)=>(
                <div key={k} className="blog-item">
                <ListItem>
                    <ListItemText primary={i.title} secondary={i.subtitle} />
                </ListItem>
                </div>
                )
            )}
        </List>
        </Typography>
     );
}

export default BlogList;