

import React from 'react';
import './Members.css';
import ListUsers from '../Project/ListUsers';
import Typography from '@mui/material/Typography';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';





function MemberGroup({data}){

    return(
        <Typography sx={{margin:1}}>
        <Accordion>
            <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
            >
            <Typography>Président</Typography>
            </AccordionSummary>
            <AccordionDetails>
                <Typography>
                    <ListUsers data={data}/>   
                </Typography>
            </AccordionDetails>
        </Accordion>
        </Typography>
    )
}



function Members(props) {
    const [users,__] = React.useState([
        {name:'Abderafie Chairi',job:"IT consultant",msg:'Take Care !!!! ,Take Care !!!! ,Take Care !!!! ,Take Care !!!! ,'},
        {name:'Jamal mansor',job:"IT consultant",msg:'Take Care !!!!',color:'#e6eaf0'},
        {name:'Imad',job:"IT consultant",msg:'Take Care !!!!'},
        {name:'Chakir',job:"IT consultant",msg:'Take Care !!!!'}
    ])
    return ( 
        <Typography sx={{display:'flex',alignItems:"center",flexDirection:'column'}}>
            <Typography sx={{fontSize:24,fontWeight:'bold'}}>Members</Typography>
                <MemberGroup data={users}/> 
            <Typography sx={{display:'grid',gridTemplateColumns:'1fr 1fr',flexWrap:'wrap'}}>
                {/* <MemberGroup data={users}/> 
                <MemberGroup data={users}/> 
                <MemberGroup data={users}/>  */}
                {/* <Typography sx={{minWidth:300}}>
                    <p className='membersTitle'>Président</p>
                    <ListUsers data={users}/>   
                </Typography>
                <Typography>
                    <p className='membersTitle'>Président</p>
                    <ListUsers data={users}/>   
                </Typography>
                <Typography>
                    <p className='membersTitle'>Président</p>
                    <ListUsers data={users}/>   
                </Typography> */}
                         
            </Typography> 
        </Typography>
     );
}

export default Members;