import * as React from 'react';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link';
import Stack from '@mui/material/Stack';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';


export default function Breadcrump({data}) {
    const hhh=["home","style",'hhhhhh']
  return (
    <Breadcrumbs maxItems={4} separator={<NavigateNextIcon fontSize="small" />}>
    {data.filter((i,k)=>k!=data.length-1).map((i,k)=>(
    <Link underline="hover" color="inherit" href="#" key={k}>
        {i}
    </Link>
    ))}

    <Typography color="text.primary">{data.at(-1)}</Typography>
    </Breadcrumbs>
  );
}
