import React from 'react'
import './Project.css';
import image from "./bg.jpg"
import { Link } from "react-router-dom";
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography'
import Chips from '../Chips';
export default function Projects(props) {
    const [projects,setProjects] = React.useState([
        1,4,5,4,5,4,5
    ])
    return (
        <div>
            <h1>Projects</h1>  
            <div className='projects'>
                {projects.map((i,k)=><Project data={i} key={k}/>)}
            </div>
        </div>
    );
}




export function Project({data}) {
	const [chips,_]=React.useState([
		{value:'Arduino',url:"/hhh"},
		{value:'Dev',url:"/hhh"},
		{value:'C++',url:"/hhh"},
		{value:'C++',url:"/hhh"},
		{value:'C++',url:"/hhh"},
		{value:'C++',url:"/hhh"},
		{value:'C++',url:"/hhh"},
		{value:'MicroPrcesseurs',url:"/hhh"},
	
	  ])
	  return (
		<Card sx={{ maxWidth: 500,margin:1,minWidth:300}}>
		  <CardMedia
			component="img"
			alt="green iguana"
			height="140"
			image={image}
		  />
		  <CardContent>
			<Typography gutterBottom variant="h5" component="div">
				<Chips data={chips} />
			  Lizard
			</Typography>
			<Typography variant="body2" color="text.secondary">
			  Lizards are a widespread group of squamate reptiles, with over 6,000
			  species, ranging across all continents except Antarctica
			</Typography>
		  </CardContent>
		  <CardActions>
			<Button size="large" component={Link} to={"/projectDetails/"+data}>More</Button>
			<Button size="small">About</Button>
		  </CardActions>
		</Card>
	  );
}
